import { useEffect, useState } from 'react';
import { useHistory } from 'react-router-dom';
import NovelInfo from '../components/NovelInfo';
import { chargeTicket, getNovelById, getTicketOfNovelId, getTicketPayment } from '../utils/Api';

const TicketChargePage = ({novelId, ...props}) => {
    const history = useHistory();
    const [novel, setNovel] = useState({});
    const [possessionCount, setPossessionCount] = useState(0);
    const [rentCount, setRentCount] = useState(0);
    const [paymentList, setPaymentList] = useState([]);

    const [selectPaymentId, setSelectPaymentId] = useState(undefined);

    useEffect( () => {
        getTicketPayment(novelId)
        .then( (response) => {
                const tckList = response.data.ticket_payment_list;
                setPaymentList(tckList);
        })
        .catch ( (error) => {

        });
    }, [novelId]);

    useEffect( () => {
        getNovelById(novelId)
        .then( (response) => {
            const novelResponse = response.data.novel;
            if (novelResponse.free === "y"  || novelResponse.hidden === "y"){
                history.push("/");
                ///     
            }
            setNovel(novelResponse);
        })
        .catch( (error) => {
            history.push('/')
            //    
        });
    }, [novelId] )


    useEffect( () => {
        getTicketOfNovelId(novelId)
        .then( (response) => {
            const ticket = response.data.ticket;
            setRentCount(ticket.rent_count);
            setPossessionCount(ticket.possession_count);
        })
        .catch( (error) => {

        })
    }, [novelId]);

    const charge = () => {
        chargeTicket(selectPaymentId)
        .then( (response) => {
            history.push("/novel/" + novelId );
        })
        .catch( (error) => {

        })
        
    }

    const renderPaymentList = () => {
        return paymentList.map( (i) => 
            <div onClick = {() => setSelectPaymentId(i.payment_id)}> 
                <div>
                    {i.total_count}
                </div>
                <div>
                    {i.pay_count} +  {i.free_count}
                </div>
                <div>
                    {i.total_price}
                </div>
            </div>
        );
    }

    return ( 
                <div>
                    <NovelInfo novel = {novel} />
                    <div>
                         : {rentCount || 0}  : {possessionCount || 0 }
                    </div>
                    <div>
                        {renderPaymentList()}
                    </div>
                    <div>
                        <button onClick={charge}>  </button>
                    </div>
                </div>
            );
}

export default TicketChargePage;