import { useEffect, useState } from 'react';
import Checkbox from './Checkbox';
import Selectbox from './Selectbox';

const NovelForm = ({novel, onsubmit, submitBtnName}) => {

    const [id, setId] = useState(null);
    const [title, setTitle] = useState('');
    const [description, setDescription] = useState('');
    const [hidden, setHidden] = useState(true);
    const [exclusive, setExclusive] = useState(false);
    const [ageGrade, setAgeGrade] = useState(-1);
    const [serialCycles, setSerialCycles] = useState([]);
    const [genres, setGenres] = useState([]);

    const max_title_length = 30;
    const max_description_length = 1000;  
    const max_genre_count = 2;

    //useEffect(() => {console.log(id,title,description,exclusive,hidden,ageGrade,serialCycles,genres)})
    
    useEffect( () => {
        if(!novel) return;
        setId(novel.novel_id);
        setTitle(novel.title);
        setDescription(novel.description);
        setHidden(novel.hidden);
        setExclusive(novel.exclusive);
        setAgeGrade(novel.age_grade);
        setSerialCycles(novel.serial_cycle);
        setGenres(novel.genre);
    }, [novel]);

    const setNovel = () => {
        return {
            title : title,
            description : description,
            hidden : hidden,
            exclusive : exclusive,
            ageGrade : ageGrade,
            genres : genres,
            serialCycles : serialCycles,
        }
    }

    const changeGenre = (genre) => {
        const index = genres.indexOf(genre);

        if(index === -1)
            setGenres([...genres, genre])
        else{
            //if(genres.length === max_genre_count)     
            setGenres(genres.filter(item => item !== genre));     
        }
    }

    const changeSerialCycles = (day) => {
        const index = serialCycles.indexOf(day);
        if(index === -1)
            setSerialCycles([...serialCycles, day])
        else
            setSerialCycles(serialCycles.filter(item => item !== day));    
        
    }       

    const renderExclusiveBox = (name, exclusives) => {
        return <Selectbox   name = {name} 
                            items = {exclusives} 
                            selectedItemValue = {exclusive}
                            selectboxName = {'exclusive'}
                            onChangeEvent = {setExclusive} />
    }

    const renderHiddenBox = (name, hiddens) => {
        return <Selectbox   name = {name} 
                            items = {hiddens} 
                            selectedItemValue = {hidden}
                            selectboxName = {'hidden'}
                            onChangeEvent = {setHidden} />
    }

    const renderAgeGradeBox = (name, ages) => {
        return <Selectbox   name = {name} 
                            items = {ages} 
                            selectedItemValue = {ageGrade}
                            selectboxName = {'age-grade'}
                            onChangeEvent = {setAgeGrade} />
    };

    const renderSerialCycleBox = (name, days) => {
        return <Checkbox name = {name} 
                         items = {days} 
                         checkedItemValues = {serialCycles}
                         checkboxName = {'serial-cycle'}
                         onChangeEvent = {changeSerialCycles} />
    }

    const renderGenreBox = (name, genre) =>{
        return <Checkbox name = {name} 
                         items = {genre} 
                         checkedItemValues = {genres}
                         checkboxName = {'genre'}
                         onChangeEvent = {changeGenre} />
    }

    return (
        <div className='novel-create-box'>
            <div className='title'>
                  
                <input type = "text" 
                    placeholder="   (30 )"
                    value={title || ''} 
                    onChange={e => setTitle(e.target.value)} 
                    size = {max_title_length+2}
                    maxLength = {max_title_length}/>
            </div>
            <div className='description'>
                  
                <textarea
                    placeholder="  (1000 )" 
                    value={description || ''} 
                    onChange={e => setDescription(e.target.value)}
                    rows = '10'
                    cols = '100' 
                    maxLength = {max_description_length}/>
            </div>
            {renderExclusiveBox( ' ', [
                { text : '', value : true },
                { text : '', value : false },
            ])}
            {renderHiddenBox( ' ', [
                { text : '', value : false },
                { text : '', value : true },
            ])}
            {renderAgeGradeBox('', [
                { text : ' ', value : -1},
                { text : '7 ', value : 7},
                { text : '12 ', value : 12},
                { text : '15 ', value : 15},
                { text : '19 ', value : 19},
            ])} 
            {renderSerialCycleBox(' ', [
                {text : '', value : ''},
                {text : '', value : ''},
                {text : '', value : ''},
                {text : '', value : ''},
                {text : '', value : ''},
                {text : '', value : ''},
                {text : '', value : ''},
            ])}
            {renderGenreBox('', [
                {text : '', value : 'fantasy'},
                {text : '', value : 'martial_arts'},
                {text : '', value : 'romance'},
            ])}    
            <button onClick={() => onsubmit(setNovel())}>
                {submitBtnName}
            </button>         
        </div>
    );
}

export default NovelForm;